/**
 * Simple Adapter
 */
import 'whatwg-fetch'
import NProgress from "nprogress"


export function simpleFetch(url, opts = {}) {
    opts.credentials = 'include'
    return new Promise((resolve, reject) => {
        window.fetch(url, opts)
            .then(res => {
                if (res.ok) {
                    res.json().then(resolve).catch(reject)
                } else {
                    NProgress.done();
                    return reject(res)
                }
            })
            .catch(() =>{
                NProgress.done();
                return reject()
            })
    });
}



