import React from 'react';
import { observer } from 'mobx-react';
import DevTools from 'mobx-react-devtools';
import { observable } from 'mobx';


import path from "path"

import Textarea from 'react-textarea-autosize';
import AutosizeInput from'react-input-autosize';

import FileInput from 'react-file-input';

var apiPath = "/api"

export const App = observer(({ store }) => (
    <div>
        { renderCurrentView(store) }
        {/*Current user:
        { store.isAuthenticated ? store.currentUser.name : "unknown user" }*/}
        {/*<DevTools />*/}
    </div>
))

function renderCurrentView(store) {
    const view = store.currentView;
    if(!store.auth.isAuthenticated) return <Login view={view} store={store} />
    if(!view) return null
    switch (view.name) {
        case "file":
            return <File view={view} store={store} />
        default : 
            return null
    }
}

const File = observer(({ view, store }) => {
    const data = view.data
    if(!view.data) return null

    const edit = store.editData.editTitle
    const editTitle = store.editData.title
    var inputfile;

    const root = data.name == ""

    return (
        <div className="main container">
            {data.name != "" && <div>
                <span className="yeLink path"><a onClick={()=>store.get("")} >Root</a></span>
                <span className="slash">/</span>
                {view.data.topPath.split("/").map(el=>el && <span>
                    <span className="yeLink path"><a onClick={()=>store.get("/"+el+"/")} >{el}</a></span>
                    <span className="slash">/</span>
                </span>)}
            </div>}
            {/*!edit && <h1 onClick={()=>store.editTitleStart(data.name)}>{ data.name == "" ? "Root" : data.name }<span className="octicon octicon-pencil edit-name" ></span></h1>*/}
            {!root && <h1><Input className="edit-name-input" onChange={e=>store.editData.title = e.target.value} value={ editTitle || data.name } onValidate={()=>store.put(data.topPath,data.name,"",editTitle)} /><span className="octicon octicon-pencil edit-name" ></span></h1>}
            {root && <h1>Root</h1>}
            <div className="option-bar">
                {data.type == "file" && <a className="btn btn-sm btn-secondary" href={apiPath+path.join(data.topPath,data.name)+"?option=download"}>Download</a>}
                {data.type == "file" && <a className="btn btn-sm btn-secondary" href={apiPath+path.join(data.topPath,data.name)+"?option=raw"} target="blank">Raw</a>}
                {data.type == "directory" && <span className="btn btn-sm btn-secondary" onClick={()=>inputfile.click()}>Upload file</span>}
                <input
                    style={{display : "none"}}
                    type="file"
                    onChange={(e)=>store.post(path.join(data.topPath, data.name), 'file', e.target.files)}
                    ref={(input) => inputfile = input } />
                {!root && <span className="btn btn-sm btn-danger" onClick={()=>store.delete(data.topPath, data.name)}>Delete</span>}            
            </div>
            <hr/>
                {data.type == "directory" ? <DirectoryView view={view} store={store} /> : <FileView view={view} store={store} />}
            <hr/>
        </div>
    )
})

const DirectoryView = observer(({ view, store }) => {
    var data = view.data
    if(!view.data) return null
    return (
        <div>
            {data.fileList.map(file=><div className="pointer" onClick={()=>store.get(path.join(data.topPath, data.name,file.name))}>
              <IconFromType mimeType={file.mimeType}/>{file.name}
            </div>)}  
            <div><Input className="add" placeholder="New file or folder/" value={store.addData.title} onChange={e=>store.addData.title=e.target.value} onValidate={()=>store.post( path.join(data.topPath, data.name), store.addData.title)}/><span className="detail">finish with a / for folder creation</span></div>
        </div>
    )
})

@observer
class FileView extends React.Component {
    render(){
        const data = this.props.view.data
        const edit = this.props.store.editData.editContent
        const editData = this.props.store.editData
        if(!this.props.view.data) return null
        return (
            <div>
                {data.content!==undefined && <div>
                    <div className="editBar">
                        {!edit && <span className="yeLink" onClick={()=>this.props.store.editContentStart(data.content)}>Edit</span>}
                        {edit && <span className="yeLink" onClick={()=>this.props.store.put( data.topPath,data.name , this.props.store.editData.content)}>Save</span>}
                        {edit && <span className="yeLink" onClick={()=>this.props.store.editStop()}>Cancel</span>}

                        <span className="float-right placeholder"><span>{editData.content.length || data.content.length} chars / {editData.content.split(" ").length-1 || data.content.split(" ").length-1} words</span></span>
                    </div>
                    <div className="editBlock">
                        {!edit && <div>{data.content || <span className="placeholder">// File is empty</span>}</div>}
                        {edit && <div><Textarea placeholder="Write here..." className="editArea" value={this.props.store.editData.content} onChange={e=>this.props.store.editData.content = e.target.value}/></div>}
                    </div>
                </div>}

                {data.mimeType.indexOf("image")!=-1 && <div>
                    <img src={apiPath+path.join(data.topPath, data.name)+"?option=raw"}/>
                </div>}
            </div>
        )
    }

}
