const Error = ({ error }) => <h1>{"Error: " + error}</h1>

@observer
class Login extends React.Component {
    @observable username = "";
    @observable password = "";
    @observable message = ""

    render() {
        return (
            <div className = "login">
                <h1>This data is protected - please login</h1>
                <form className="box" action="javascript:void(0);">
                    {this.message && <div className="alert alert-danger">{this.message}</div>}
                    <input placeholder="Username" className="form-control" value={this.username} onChange={e => this.username = e.target.value} />
                    <input placeholder="Password" type="password" className="form-control" value={this.password} onChange={e => this.password = e.target.value} />
                    <button className="btn btn-block btn-success" onClick={this.onLogin.bind(this)}>Login</button>
                </form>
            </div>
        )
    }

    onLogin(){
        this.props.store.login(this.username, this.password).then().catch(res => {
            this.message = "Wrong creds..."
            this.username = ""
            this.password = ""
        })
    }
}

class Input extends React.Component {
  _handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      !!this.props.onValidate && this.props.onValidate()
    }
  }

  render() {
    return <AutosizeInput type="text" {...this.props} onKeyPress={this._handleKeyPress} />
  }
}

class IconFromType extends React.Component {

  render() {
    if(!this.props.mimeType) return <span className="octicon octicon-file-directory"></span>
    var mimeType = this.props.mimeType.split("/")
    var iconClass = "octicon "
    if(mimeType[0] == "image") iconClass += "octicon-file-media"
    else if(mimeType[0] == "text") iconClass += "octicon-file-text"
    else if(mimeType[1] == "pdf") iconClass += "octicon-file-pdf"
    else if(mimeType[1] == "x-rar-compressed" && mimeType[1] == "zip" ) iconClass += "octicon-file-zip"
    else iconClass += "octicon-file"
    return <span className={iconClass}></span>
  }

}


