import { observable, computed, action } from 'mobx';
import { fromPromise } from 'mobx-utils';
import NProgress from "nprogress"
import path from "path"
import Cookies from "js-cookie"

class ViewStore {
    fetch = null;

    @observable currentUser = null;
    @observable currentView = null;
    @observable editData = {
        editContent : false,
        editTitle : false,
        content : "",
        title : ""
    };
    @observable addData = {
        title : "",
    };
    @observable auth = {
        isAuthenticated : true,
        token : ""
    }

    constructor(fetch) {
        this.fetch = fetch
    }

    // @computed get isAuthenticated() {
    //     return this.currentUser !== null
    // }

    @computed get currentPath() {
        if(!this.currentView) return window.location.pathname
        const yePath= path.join(this.currentView.data.topPath, this.currentView.data.name)
        switch(this.currentView.name) {
            case "file": return `${yePath}`
        }
    }

    // @action showOverview() {
    //     this.currentView = {
    //         name: "overview",
    //         documents: fromPromise(this.fetch(`/json/documents.json`))
    //     }
    // }

    // @action showDocument(documentId) {
    //     console.log(documentId)
    //     this.currentView = {
    //         name: "document",
    //         documentId,
    //         document: fromPromise(
    //             this.fetch(`/json/${documentId}.json`)
    //             // this.isAuthenticated
    //             //     ? this.fetch(`/json/${documentId}.json`)
    //             //     : Promise.reject("Authentication required")
    //         )
    //     }
    // }

    @action get(path) {
        NProgress.start();
        this.fetch(`/api${path}`,{
            headers : {
                token : this.auth.token
            }
        }).then(res => {
            NProgress.done();
            this.currentView = {
                name: "file",
                data: res 
            }
            this.editStop()
        }).catch(res => {
            if (res.status == 401){
                this.auth.isAuthenticated = false
                this.auth.token = ""
            }
        })
    }

    @action put(topPath, name, content = "", newName = "") {
        NProgress.start();
        var body = {content, name : newName}
        var yePath = path.join(topPath, name)
        this.fetch(`/api${yePath}`,
        {
            method : "PUT",
            body : JSON.stringify(body),
            headers : {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'token' : this.auth.token
            }            
        }).then(res => {
            NProgress.done();
            if(content) this.currentView.data.content = content
            if(newName) this.currentView.data.name = newName
            this.editStop()
        })
    }

    @action post(topPath, name, fileList) {
        var type = "file"
        if(name.slice(-1)=="/" && !fileList) type = "directory"
        console.log(type)
        NProgress.start()

        var yePath = path.join(topPath)
        var data
        var headers

        if(fileList){
            data = new FormData()
            data.append('file', fileList[0])
            headers = {
                'token' : this.auth.token
            }   
        }else{
            data = JSON.stringify({name})
            headers = {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'token' : this.auth.token
            }   
        }
        this.fetch(`/api${yePath}`,
        {
            method : "POST",
            body : data,
            headers       
        }).then(res => {
            // if(type=="directory") res.name = res.name.slice(0,-1)
            // Object.assign(res,{type, topPath})
            NProgress.done();
            this.currentView.data.fileList.push(res)
            this.editStop()
        })
    }

    @action delete(topPath,name) {
        NProgress.start()
        var yePath = path.join(topPath,name)
        this.fetch(`/api${yePath}`,
        {
            method : "DELETE",
            headers : {
                'token' : this.auth.token
            }             
        }).then(res => {
            this.get(topPath)
            NProgress.done();
            this.editStop()
        })
    }

    @action login(username, password) {
        return new Promise((resolve, reject) => {
            this.fetch(`/api/login`,
            {
                method : "POST",
                body : JSON.stringify({username, password}),
                headers : {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }              
            }).then(res => {
                this.auth.isAuthenticated = true,
                this.auth.token = res.token
                Cookies.set('token', res.token);
                this.get(window.location.pathname)
                resolve()
            }).catch(()=> {
                Cookies.remove('token')
                reject()
            })
        })
    }

    @action editContentStart(content) {
        this.editData = {
            editContent : true,
            editTitle : false,
            content : content,
            title:""
        }
    }

    @action editTitleStart(title) {
        this.editData = {
            editContent : false,
            editTitle : true,
            title : title,
            content : ""
        }
                console.log(this.editData.editTitle)
    }

    @action editStop() {
        this.editData = {
            editContent : false,
            editTitle : false,
            content : "",
            title:""
        }
        if ("activeElement" in document) document.activeElement.blur();
        this.addData.title = ""
    }

    // @action performLogin(username, password, callback) {
    //     this.fetch(`/json/${username}-${password}.json`)
    //         .then(user => {
    //             this.currentUser = user
    //             callback(true)
    //         })
    //         .catch(err => {
    //             callback(false)
    //         })
    // }
}

export default ViewStore;



